.card {
    width: 400px;
    height: 50px;

    background: #FFFFFF;
    border-radius: 12px;
    display: flex;
    align-items: center;
    padding: 0 20px 0 20px;
    margin-bottom: 10px;
    justify-content: space-between;
}

.card p {
    color: black;
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
}

.box__coefficient {
    width: 130px;
    height: 30px;
    border: 1px solid black;
    background-color: cadetblue;
}